.container{
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.widget-image {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center;
  position: relative;
  z-index: 1;

  &.placeholder {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.8;
    }
  }
}

.resize-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
  height: 100%;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 24px;
  border: 2px dashed #ccc;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  background: rgba(0, 0, 0, 0.02);

  &:hover {
    border-color: #2196F3;
    background: rgba(33, 150, 243, 0.05);

    .hint {
      color: #2196F3;
    }
  }

  .hint {
    font-size: 14px;
    color: #666;
    transition: color 0.2s;
  }
}

.description {
  margin-top: 8px;
  font-size: 14px;
  color: #666;
  text-align: center;
}
